const trap = document.getElementById("trap");
const btn = document.querySelector("button");
btn.addEventListener("click", function () {
  if (this.getAttribute("aria-pressed") === "true") {
    keyboardTrap.untrap();
  } else {
    keyboardTrap.trap(this.parentNode);
  }
});
